<style lang="less" scoped>
  @import './subject.less';
</style>

<template>
<div class="bg_ff">

    <Row class="app-resource-group-title">
         <h3></h3>
    </Row>
    <Row class="subject-content">
         <Table :columns="columns" :data="subjects"></Table>
    </Row>

  </div>
</template>

<script>
import { getAppSubjectData } from "@/services/appService.js";

export default {
  name: "appSubject",
  components: {},
  props: {
    listId: {
      type: String
    }
  },
  data() {
    return {
      columns: [
        {
          title: "",
          key: "accountName",
          align: "center"
        },
        {
          title: "",
          key: "classify",
          align: "center",
          render: (h, params) => {
            if (params.row.classify === 1) {
              return h("span", {}, "");
            } else {
              return h("span", {}, "");
            }
          }
        },
        {
          title: "",
          key: "dataCollection",
          align: "center",
          render: (h, params) => {
            let collectionStatus = [],
              subject = params.row;

            //
            subject.calcRels.forEach(val => {
              collectionStatus.push(val.matchDirectionByDebit);
            });

            // 
            if (subject.classify === 1) {
              if (/1/.test(collectionStatus) && /-1/.test(collectionStatus)) {
                return h("span", {}, "");
              } else if (/1/.test(collectionStatus)) {
                return h("span", {}, "");
              } else if (/-1/.test(collectionStatus)) {
                return h("span", {}, "");
              }
            } else {
              if (/1/.test(collectionStatus) && /-1/.test(collectionStatus)) {
                return h("span", {}, "");
              } else if (/1/.test(collectionStatus)) {
                return h("span", {}, "");
              } else if (/-1/.test(collectionStatus)) {
                return h("span", {}, "");
              }
            }
          }
        }
      ],
      subjects: []
    };
  },
  methods: {},
  created() {
    getAppSubjectData(this.listId).then(res => {
      if (res.status === 200) {
        this.subjects = res.data;
      } else {
        //todo
        //
      }
    });
  }
};
</script>




